@import '../../util/variable.scss';
.header {
  padding: 0;
  margin: 0;
  background-color: $background-color-main;

  &__content {
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    padding: 17px 16px;
    max-width: 1290px;
    margin: auto;

    @media (min-width: 786px) {
      padding: 17px 32px;
    }

    @media (min-width: 1024px) {
      padding: 17px 60px;
    }
  }

  &__buttons {
    display: flex;
    gap: 10px;
  }
}
